#visai-logo {
    position: absolute;
    left: 5px;
    top: 4px;
    width: 60px !important;
    z-index: 2;

    @include tablet {
        left: 20px;
        top: 10px;
        width: 130px !important;
    }
}

#fade {
    position: absolute;
    left: -4px;
    top: 0px;
    width: 65px !important;
    z-index: 1;
    background: #fef1d1;
    width: 71px !important;
    height: 78px !important;
    filter: blur(3px);
    border-radius: 0% 0% 50% 0%;
}

#venba-logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 10px;
    width: 31% !important;
    min-width: 260px;
    z-index: 2;

    @include tablet {
        top: 35px;
    }

}

body {
    background: #FFF2D5;
    background: url(../img/bg.png);
    background-size: 36%;

    @include tablet {
        background-size: 23%;
    }
}


#SA_main-body {
    overflow: hidden;
    background: transparent;
    @include tablet {
        background-size: 23%;
    }
}

#header-art-image {
    width: 162% !important;
    max-width: none !important;
    margin-left: 50%;
    transform: translateX(-50%);
    position: relative;

    @include tablet {
        transform: none;
        margin-left: 0 !important;
        width: 100% !important;
        max-height: 110vh;
        object-fit: cover;
        object-position: top;
    }
}

::selection {
    color: #fff2d5;
    background: #f23662;
    text-shadow: none;
}


.banner ::selection,
#SA_footer ::selection {
    color: #f23662 !important;
    background: #fff2d5 !important;
    text-shadow: none !important;
}




#SA_main-body .banner {
    background: #EA2949;
    background: url(../img/banner-bg-540w.png);
    // min-height: 278px;
    width: 120%;
    margin: -5.2% -10% 0 -10%;

    @include desktop {
        margin: -5% -10% 0 -10%;
    }

    padding: 40px 10%;
    transform: rotate(-2.86deg);
    z-index: 2;
    position: relative;
    background-size: 500px;
    text-shadow: 0px 3px #cf2142;
    //text-shadow: 0px 3px 1px #00000040;

    p {
        padding: 0 20px;
        text-align: center;
        line-height: 1.2;
        max-width: 600px;
        margin: 0 auto;
        font-size: 2rem;
        color: #fff;
    }
}

.banner .icon-grid img {
    filter: drop-shadow(0px 2px 0px #cf2142);
    //  filter: drop-shadow(0px 2px 1px #00000040);
}

.banner .icon-grid p {
    padding: 0 !important;
}

#banner-2 {
    margin: 10% -10% -5% -10% !important;

    @include tablet {
        margin: 5% -10% 5% -10% !important;
    }

    transform: rotate(2.86deg) !important;
}

#banner-3 {}

#section-1 {
    margin-top: 50px;
    margin-bottom: 50px;

    @include tablet {
        margin-top: 100px;
        margin-bottom: 100px;
    }
}

.SA_text-wrap {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p,
    ul {
        @include text-stroke(3, #FFF2D5);
    }
}


.image-text-layout {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p,
    ul {
        @include text-stroke(3, #FFF2D5);
    }
}

.icon-grid {
    display: flex;
    // flex-direction: row;
    // align-items: flex-start;
    padding: 0 25px;
    column-gap: 28px;
    row-gap: 25px;

    @include tablet {
        column-gap: 35px;
    }

    flex-wrap: wrap;
    margin: 35px auto 0 auto;
    max-width: 1200px;
    justify-content: center;


}

.icon-grid-item {
    display: flex;
    align-items: center;

    text-decoration: none;

    p {
        margin: 0 !important;
        font-size: 1.2rem !important;

        @include tablet {
            font-size: 1.4rem !important;
        }

        position: relative;
        line-height: 1.2 !important;
    }
}

a.icon-grid-item {
    cursor: pointer;
}


a.icon-grid-item:hover p::after {
    content: "-–---–----–---–---–----–---–---–----–---–---–----–--";
    display: block;
    position: absolute;
    bottom: -0.08em;
    left: 0;
    width: 100%;
    border-radius: 25px;
    text-shadow: none;
    white-space: nowrap;
    overflow: hidden;
    line-height: 0.1;
    letter-spacing: -0.07em;
    font-size: 1.4em;
    transform: rotate(-1deg);
}

.icon-grid-item img {
    flex: 1;
    height: 40px;
    margin-right: 10px;
    display: inline;
}

// To block the video below in cases where the preview doesnt load in time
.SA_video-overlay:before {
    content: "";
    background: #000;
    position: absolute;
    width: 100%;
    height: 100%;
    display: block;
    top: 0;
    left: 0;
    opacity: 1;
    z-index: -1;
}

.SA_video-overlay:after {
    content: "";
    background: #000;
    position: absolute;
    width: 100%;
    height: 100%;
    display: block;
    top: 0;
    left: 0;
    opacity: 0.5;
    z-index: 1;
}

.SA_play-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 25px;
    text-align: center;
    background: transparent;
    border: 3px solid #fff;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    padding: 0;
    margin: 0;
    font-family: #{$captionfont};
    cursor: pointer;
    z-index: 2;
    // box-shadow: rgba(0, 0, 0, 0.15) 0px 6px 18px -2px, rgba(0, 0, 0, 0.4) 0px 3px 7px -3px;
    pointer-events: none;
    color: #000;
}

.SA_play-button::after {
    content: "";
    width: 0;
    height: 0;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-left: 25px solid #fff;
    display: block;
    position: absolute;
    top: 52%;
    left: 56%;
    transform: translate(-50%, -50%);
}

.SA_inline-video-wrapper {
    padding-top: 56.25%;
}

.SA_video-preview {
    z-index: 1;
}

.SA_inline-video,
.SA_video-preview {
    cursor: pointer;
    // aspect-ratio: 16 / 9;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

}

.SA_inline-video[data-autoplay="true"] {
    cursor: auto;
}

.image-text-layout {
    align-items: center;

    img {
        border-radius: 4px
    }
}

@include mobile-only {
    .image-text-layout-1 {
        display: flex;
        flex-direction: column-reverse;
    }
}

#swiper-wrapper {
    @include desktop {
        margin-top: 150px;
    }
}

.swiper {
    width: 100%;
    height: 100%;
    border-radius: 4px;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

:root {
    --swiper-theme-color: #fff
}

#SA_footer {
    margin-top: 50px;
    display: flex;
    justify-content: flex-end;
    gap: 50px;
    color: #fff;
    font-size: 0.8em;
    background: #ea2949;
    background: url(../img/banner-bg-540w.png);
    background-size: 500px;
    padding: 15px 25px;

    a {
        color: #fff;
        text-decoration: none;

        &:hover {
            text-decoration: underline;
            color: #fff;
        }
    }
}


.video-container {
    position: relative;
    padding-bottom: 56.25%;
    border-radius: 4px;
    overflow: hidden;
}

.video-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.SA_h1 {
    font-size: 2.488rem;

    @include tablet {
        font-size: 3.488rem;
    }

    @include text-stroke(3, #FFF2D5);
}

.SA_h2 {
    font-size: 1.78rem;

    @include tablet {
        font-size: 2.488rem;
    }
}